.ball {
  position: absolute;
  background: -webkit-gradient(linear, left top, left bottom, from(#24c0db), to(#1c58b3));
  background: linear-gradient(to bottom, #24c0db, #1c58b3);
  -webkit-box-shadow: 0 0 5px #208c95 inset;
          box-shadow: 0 0 5px #208c95 inset;
  border-radius: 50%;
  -webkit-animation: shrink 1.2s linear 1;
          animation: shrink 1.2s linear 1;
  opacity: 1;
}

.mouse {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  text-align: center;
  padding: 0 20px;
  width: 100%;
  color: white;
  font-size: 40px;
  text-transform: uppercase;
  font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  font-weight: 700;
  letter-spacing: 0.8px;
  z-index: -1;
}

@-webkit-keyframes shrink {
  0% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
  20% {
    -webkit-transform: scale(1);
            transform: scale(1);
    opacity: 1;
  }
  100% {
    -webkit-transform: scale(0);
            transform: scale(0);
    opacity: 0;
  }
}

@keyframes shrink {
  0% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
  20% {
    -webkit-transform: scale(1);
            transform: scale(1);
    opacity: 1;
  }
  100% {
    -webkit-transform: scale(0);
            transform: scale(0);
    opacity: 0;
  }
}

body, #root {
  height: 100%;
  width: 100%;
  background-color: #121212;
  overflow: hidden;
}

.App {
  height: 100%;
  width: 100%;
}
